import React from 'react';

export default function Center(props){
  const peoples=props.peoples;
  const selectplayer=props.selectplayer;
  const active=props.active;
  const changeplayer=props.changeplayer;
  const sort=props.sort;
  const scoreplayer=props.scoreplayer;
  const activesort=props.activesort;
  const changesort=props.changesort;
  const listitems=peoples.map((value,index)=>
    <div className="peopleslist" key={index}>
      {selectplayer!==value.id?
      <div className="peoples" onClick={changeplayer.bind(this,value.id)}>
        <div className="thumb">
          <img src={value.thumb} alt="" width="100%"/>
        </div>
        <div className="peoplename">
          {value.name}
        </div>
        <div className="fraction">
           : {value.fraction}
        </div>
        {value.score?
        <div className="score">
          
        </div>:
        <div className="noscore" onClick={scoreplayer.bind(this)}>
          
        </div>
        }
      </div>:
      <div className="peoples" style={active}>
        <div className="thumb">
          <img src={value.thumb} alt="" width="100%"/>
        </div>
        <div className="peoplename">
          {value.name}
        </div>
        <div className="fraction">
           : {value.fraction}
        </div>
        {value.score?
          <div className="score">
            
          </div>:
          <div className="noscore">
            
          </div>
        }
      </div>
      }
    </div>
  )
  return(
    <div className="center">
      <div className="head">
        {sort?
        <span className="text" style={activesort}>
          
        </span>:
        <span className="text" onClick={changesort}>
          
        </span>
        }
        {!sort?
        <span className="text" style={activesort}>
          
        </span>:
        <span className="text" onClick={changesort}>
          
        </span>
        }
      </div>
      <div className="allplayey">
        {listitems}
      </div>
    </div>
  )
}


