import React from 'react';
import Centerspace from './center.js'
import Rightspace from './right.js'
import Dialogspace from './dialog.js'
import './score.scss';

export default class Score extends React.Component {
  constructor(props){
    super(props)
    this.state={
      Country:[
        {name:'',thumb:'https://data.ehometd.com/files/0/1003/bx.jpg?v=77975',id:1},
        {name:'',thumb:'https://data.ehometd.com/files/0/1003/els.jpg?v=77975',id:2},
        {name:'',thumb:'https://data.ehometd.com/files/0/1003/bx.jpg?v=77975',id:3},
        {name:'',thumb:'https://data.ehometd.com/files/0/1003/els.jpg?v=77975',id:4},
        {name:'',thumb:'https://data.ehometd.com/files/0/1003/bx.jpg?v=77975',id:5},
        {name:'',thumb:'https://data.ehometd.com/files/0/1003/els.jpg?v=77975',id:6},
        {name:'',thumb:'https://data.ehometd.com/files/0/1003/bx.jpg?v=77975',id:7},
        {name:'',thumb:'https://data.ehometd.com/files/0/1003/els.jpg?v=77975',id:8},
        {name:'',thumb:'https://data.ehometd.com/files/0/1003/bx.jpg?v=77975'},
        {name:'',thumb:'https://data.ehometd.com/files/0/1003/els.jpg?v=77975'},
        {name:'',thumb:'https://data.ehometd.com/files/0/1003/bx.jpg?v=77975'},
        {name:'',thumb:'https://data.ehometd.com/files/0/1003/els.jpg?v=77975'},
      ],
      peoples:[
        {name:'',thumb:'https://data.ehometd.com/files/0/1003/.jpg?v=77975',
          score:true,fraction:4.8,id:1},
        {name:'',thumb:'https://data.ehometd.com/files/0/1003/2.jpg?v=77975',
          score:true,fraction:4.8,id:2},
        {name:'',thumb:'https://data.ehometd.com/files/0/1003/3.jpg?v=77975',
          score:true,fraction:4.8,id:3},
        {name:'',thumb:'https://data.ehometd.com/files/0/1003/.jpg?v=77975',
          score:false,fraction:4.8,id:4},

      ],
      infomations:[
        {name:'',content:''},
        {name:'',content:'~'},
        {name:'',content:'666666'},
        {name:'',content:''},
        {name:'',content:','},
        {name:'',content:''},
        {name:'',content:'~'},
      ],
      listtype:true,
      active:{
        background:'rgba(172,132,84,0.6)'
      },
      activesort:{
        color:'#ac8454'
      },
      selectcountry:1,
      selectPlayer:1,
      sort:true,
      dialog:true,
      playerinfo:{
        pic:'https://data.ehometd.com/files/0/1003/.jpg?v=77975',
        thumb:[
          'https://data.ehometd.com/files/0/1003/.jpg?v=77975',
          'https://data.ehometd.com/files/0/1003/2.jpg?v=77975',
          'https://data.ehometd.com/files/0/1003/3.jpg?v=77975',
        ],
        name:'',
        Ename:'Oscar',
        Country:'',
        birday:'1991-9-9',
        height:'181cm',
        weight:'66kg',
        alias:',',
        team:'',
        place:',,',
        teacher:'',
        exp:[
          '199199' +
          '',
          '200917' +
          '',
          '20106475' +
          '',
          '201190:0',
          '201272500 ' +
          '8',
          '2013',
          '201463:1' +
          '',
          '20153' +
          '514/15',
          '4203' +
          '38',
          '20161223' +
          '',
        ],
      },
      personalscore:{
        overallscore:'100.00',
        ability:'100.00',
        fuse:'100.00',
        plus:'100.00',
        keep:'100.00',
        state:'100.00',
      }
    }
  }
  changelisttype =()=>{
    this.setState((res)=>({
      listtype:!res.listtype
    }))
  }
  changecountry=(value)=>{
    this.setState({
      selectcountry:value,
    })
  }
  changeselectPlayer=(value)=>{
    this.setState({
      selectPlayer:value,
    })
  }
  changesort=()=>{
    this.setState((res)=>({
      sort:!res.sort,
    }))
  }
  changescore=(event)=>{
    event.persist();
    var value=event.currentTarget.value
    var name=event.currentTarget.name
    // this.setState(Object.assign({}, this.state.personalscore, {[name]:value}))
    let data=this.state.personalscore;
    data[name]=value;
    this.setState({data});
  }
  scoreplayer=(event)=>{
    event.stopPropagation();
    this.setState((res)=>({
      dialog:!res.dialog
    }))
  }
  render() {
    return (
      <div id="score">
        {this.state.dialog?
        <div className="nodialog">
          <Leftspace country={this.state.Country}
                     listtype={this.state.listtype}
                     active={this.state.active}
                     change={this.changelisttype}
                     selectcountry={this.state.selectcountry}
                     changecountry={this.changecountry}/>
          <Centerspace peoples={this.state.peoples}
                       selectplayer={this.state.selectPlayer}
                       active={this.state.active}
                       activesort={this.state.activesort}
                       changeplayer={this.changeselectPlayer}
                       sort={this.state.sort}
                       changesort={this.changesort}
                       scoreplayer={this.scoreplayer}
          />
          <Rightspace infos={this.state.infomations }/>
        </div>:
        <Dialogspace playerinfo={this.state.playerinfo}
                     personalscore={this.state.personalscore}
                     changescore={this.changescore}
                     scoreplayer={this.scoreplayer}/>
        }
      </div>
    )
  }
}

function Leftspace(props){
  const country=props.country;
  const listtype=props.listtype;
  const active=props.active
  const changeevent=props.change
  const selectcountry=props.selectcountry
  const changecountry=props.changecountry
  const listitems=country.map((value,index)=>
    <div className="country" key={index}>
      {selectcountry!==value.id?
      <div className="countryinfo" onClick={changecountry.bind(this,value.id)}>
        <div className="thumb">
          <img src={value.thumb} alt="" width="100%"/>
        </div>
        <div className="name">{value.name}</div>
      </div>:
      <div className="countryinfo2">
        <div className="thumb">
          <img src={value.thumb} alt="" width="100%"/>
        </div>
        <div className="name">{value.name}</div>
      </div>
      }
    </div>
  )
  const listitems2=country.map((value,index)=>
    <div className="country2" key={index}>
      {selectcountry !== value.id ?
        <div className="countryinfo" onClick={changecountry.bind(this, value.id)}>
          <div className="thumb2">
            <img src={value.thumb} alt="" width="100%"/>
          </div>
          <div className="name2">{value.name}</div>
        </div> :
        <div className="countryinfo2">
          <div className="thumb2">
            <img src={value.thumb} alt="" width="100%"/>
          </div>
          <div className="name2">{value.name}</div>
        </div>
      }
    </div>
  )
  return(
    <div className="left">
      <div className="header">
        <div className="search">
          <input />
          <div className="icon">
            <img src="https://data.ehometd.com/files/0/1003/.png?v=77975" alt="" width="30px"/>
          </div>
        </div>
        <div className="specialbtn">
          {listtype?
          <div className="btn" style={active}>
            <img src="https://data.ehometd.com/files/0/1003/.png?v=77975" alt="" width="100%"/>
          </div>:
          <div className="btn" onClick={changeevent}>
            <img src="https://data.ehometd.com/files/0/1003/.png?v=77975" alt="" width="100%"/>
          </div>
          }
          {!listtype?
          <div className="btn" style={active}>
            <img src="https://data.ehometd.com/files/0/1003/.png?v=77975" alt="" width="100%"/>
          </div>:
          <div className="btn" onClick={changeevent}>
            <img src="https://data.ehometd.com/files/0/1003/.png?v=77975" alt="" width="100%"/>
          </div>
          }
          <div className="btn">
            <img src="https://data.ehometd.com/files/0/1003/.png?v=77975" alt="" width="100%"/>
          </div>
        </div>
      </div>
      {listtype?listitems:listitems2}
    </div>
  )
}



