import React from 'react';
import {Link} from 'react-router-dom'

export default class Head extends React.Component {
  constructor(props){
    super(props)
    this.state={
      active:{
        color:'#f5a623',
      },
      path:window.location.pathname,
    }
  }
  componentWillReceiveProps(props){
    this.setState({path:window.location.pathname})
  }
  render() {
    return (
      <div id="head">
        <div className="left">
          <div className="logo">
            logo
          </div>
          <div className="internet">
            
            <div className="quan"/>
          </div>
        </div>
        <div className="alljump">
          <span className="jump">
            {this.state.path==='/'?
            <Link to="/" style={this.state.active}></Link>:
            <Link to="/" ></Link>
            }
          </span>
          <span className="jump">
            {this.state.path === '/news' ?
            <Link to="/news" style={this.state.active}></Link> :
            <Link to="/news"></Link>
            }
          </span>
          <span className="jump">
           {this.state.path === '/score' ?
             <Link to="/score" style={this.state.active}></Link>:
             <Link to="/score" ></Link>
           }
          </span>
          <span className="jump">
           {this.state.path === '/operationrecord' ?
             <Link to="/operationrecord" style={this.state.active}></Link>:
             <Link to="/operationrecord" ></Link>
           }
          </span>
          <span className="jump">
           {this.state.path === '/operationnotice' ?
             <Link to="/operationnotice" style={this.state.active}></Link>:
             <Link to="/operationnotice" ></Link>
           }
          </span>
          <span className="jump">
            {this.state.path === '/login' ?
              <Link to="/login" style={this.state.active}></Link>:
              <Link to="/login" ></Link>
            }
          </span>
        </div>
      </div>
    )
  }
}


