import React from 'react';
import ReactSwiper from 'reactjs-swiper';
import './news.scss';

export default class Home extends React.Component {
  constructor(props){
    super(props)
    this.state={
      news:[
        {title:'!',time:'3-20'},
        {title:'!',time:'3-20'},
        {title:'!',time:'3-20'},
        {title:'!',time:'3-20'},
        {title:'!',time:'3-20'},
        {title:'!',time:'3-20'},
        {title:'!',time:'3-20'},
      ],
      detailnews:{
        title:'',
        content:[
          {
            value:'',
            thumb:'https://data.ehometd.com/files/0/1003/2.jpg?v=77975'
          },
          {
            value:'',
            thumb:'https://data.ehometd.com/files/0/1003/2.jpg?v=77975'
          },
        ],
      },
      Dialog:false,
    }
  }
  changedialog=()=>{
    this.setState((res)=>({
      Dialog:true
    }))
  }
  closedialog=()=>{
    this.setState((res)=>({
      Dialog:false
    }))
  }
  render() {
    return (
      <div id="news">
        <Carousel/>
        <div className="newsmain">
          <News news={this.state.news} change={this.changedialog}/>
          <div className="Gap"/>
          <News news={this.state.news} change={this.changedialog}/>
        </div>
        {this.state.Dialog?
        <Dialog news={this.state.detailnews} change={this.closedialog}/>:''
        }
      </div>
    )
  }
}
function Dialog(props){
  const news=props.news;
  const listitems=news.content.map((value,index)=>
    <div className="content" key={index}>
      <div className="words">
        {value.value}
      </div>
      <div className="thumb">
        <img src={value.thumb} alt=""/>
      </div>
    </div>
  )
  return(
  <div className="Dialog">
    <div className="close" onClick={props.change}>
      <img src="https://data.ehometd.com/files/0/1003/.png?v=77975" alt="" width="30px"/>
    </div>
    <div className="title">
      {news.title}
    </div>
    {listitems}
  </div>
  )
}
function News(props){
  const list=props.news;
  const listitems=list.map((value,index)=>
    <div key={index} className="new" onClick={props.change}>
      <span className="left">{value.title}</span>
      <span className="right">{value.time}</span>
    </div>
  );
  return(
    <div className="newslist">
      {listitems}
    </div>
  )
}
function Carousel(){
  const items = [{
    image: 'https://data.ehometd.com/files/0/1003/4.jpg?v=7123',
  }, {
    image: 'https://data.ehometd.com/files/0/1003/4.jpg?v=456',
  }, {
    image: 'https://data.ehometd.com/files/0/1003/4.jpg?v=1222',
  }];
  const swiperOptions = {
    preloadImages: true,
    autoplay: 3000,
    autoplayDisableOnInteraction: false
  };
  return(
    <ReactSwiper swiperOptions={swiperOptions} showPagination items={items}
                 className="carousel" />
  )
}



