import React from 'react';
import './operationrecord.scss';

export default class Operationrecord extends React.Component {
  constructor(props){
    super(props)
    this.state={
      record:[
        {content:'~',time:'2018-01-01 11:11'},
        {content:'~',time:'2018-01-01 11:11'},
        {content:'~',time:'2018-01-01 11:11'},
        {content:'~',time:'2018-01-01 11:11'},
        {content:'~',time:'2018-01-01 11:11'},
        {content:'~',time:'2018-01-01 11:11'},
        {content:'~',time:'2018-01-01 11:11'},
        {content:'~',time:'2018-01-01 11:11'},
        {content:'~',time:'2018-01-01 11:11'},
        {content:'~',time:'2018-01-01 11:11'},
        {content:'~',time:'2018-01-01 11:11'},
      ]
    }
  }
  render() {
    return (
      <div id="Operationrecord">
        <div className="record">
          <div className="title">
            
          </div>
          <Record record={this.state.record}></Record>
        </div>
      </div>
    )
  }
}
function Record(props){
  const record=props.record;
  const listitems=record.map((value,index)=>
    <div className="content" key={index}>
      <span className="left">{value.content}</span>
      <span className="right">{value.time}</span>
    </div>
  )
  return (
    <div className="recordlist">
      {listitems}
    </div>
    )
}


