import React from 'react';
import './operationnotice.scss';

export default class Operationnotice extends React.Component {
  constructor(props){
    super(props)
    this.state={
      notice:[
        {title:'',time:'2018-3-3'},
        {title:'',time:'2018-3-3'},
      ]
    }
  }
  render() {
    return (
      <div id="operationnotice">
        <div className="notice">
          <div className="title">
            
          </div>
          <Notice notice={this.state.notice}/>
        </div>
      </div>
    )
  }
}
function Notice(props){
  const notice=props.notice;
  const listitems=notice.map((value,index)=>
    <div className="noticelist" key={index}>
      <span className="left">
        {value.title}
      </span>
      <span className="right">
        {value.time}
      </span>
    </div>
  )
  return(
    <div className="noticelist">
      {listitems}
      <div className="noticelist"/>
    </div>
  )
}


