import React from 'react';
import ReactSwiper from 'reactjs-swiper';

export default function Dialog(props){
  const playerinfo=props.playerinfo;
  const exp=props.playerinfo.exp;
  const personalscore=props.personalscore;
  const changescore=props.changescore;
  const scoreplayer=props.scoreplayer;
  const listitems=exp.map((value,index)=>
    <div className="eachexp" key={index}>
      {value}
    </div>
  )
  return(
    <div className="dialog">
      <div className="close" onClick={scoreplayer.bind(this)}>
        <img src="https://data.ehometd.com/files/0/1003/.png?v=77975" alt="" width="100%"/>
      </div>
      <div className="personalinfo">
        <div className="playerinfo">
          <div className="thumb">
            <Carousel infos={playerinfo}/>
          </div>
          <div className="infos">
            <div className="name">{playerinfo.name}</div>
            <div className="info">:{playerinfo.Ename}</div>
            <div className="info">:{playerinfo.Country}</div>
            <div className="info">:{playerinfo.birday}</div>
            <div className="info">:{playerinfo.height}</div>
            <div className="info">:{playerinfo.weight}</div>
          </div>
          <div className="infos2">
            <div className="info">:{playerinfo.alias}</div>
            <div className="info">:{playerinfo.team}</div>
            <div className="info">:{playerinfo.place}</div>
            <div className="info">:{playerinfo.teacher}</div>
          </div>
        </div>
        <div className="exp">
          <div className="title">
            
          </div>
          <div className="list">
            {listitems}
          </div>
        </div>
        <div className="score">
          <div className="title">
            
          </div>
          <div className="fraction">
            <span className="text"></span>
            <input type="range"  defaultValue={personalscore.overallscore}
                   onChange={changescore.bind(this,)} min="0" max="100"
                   name="overallscore"/>
            <input type="text" disabled value={personalscore.overallscore}/>
          </div>
          <div className="fraction">
            <span className="text"></span>
            <input type="range" defaultValue={personalscore.ability}
                   onChange={changescore.bind(this,)} min="0" max="100"
                   name="ability"/>
            <input type="text" disabled value={personalscore.ability}/>
          </div>
          <div className="fraction">
            <span className="text"></span>
            <input type="range" defaultValue={personalscore.fuse}
                   onChange={changescore.bind(this,)} min="0" max="100"
                   name="fuse"/>
            <input type="text" disabled value={personalscore.fuse}/>
          </div>
          <div className="fraction">
            <span className="text"></span>
            <input type="range" defaultValue={personalscore.plus}
                   onChange={changescore.bind(this,)} min="0" max="100"
                   name="plus"/>
            <input type="text" disabled value={personalscore.plus}/>
          </div>
          <div className="fraction">
            <span className="text"></span>
            <input type="range" defaultValue={personalscore.keep}
                   onChange={changescore.bind(this,)} min="0" max="100"
                   name="keep"/>
            <input type="text" disabled value={personalscore.keep}/>
          </div>
          <div className="fraction">
            <span className="text"></span>
            <input type="range" defaultValue={personalscore.state}
                   onChange={changescore.bind(this,)} min="0" max="100"
                   name="state"/>
            <input type="text" disabled value={personalscore.state}/>
          </div>
        </div>
        <div className="btn">
          <div className="confirm">
            
          </div>
          <div className="rewrite">
            
          </div>
        </div>
      </div>
    </div>
  )
}

function Carousel(props){
  const thumb=props.infos.thumb;
  var items=[];
  thumb.map((value,index)=>
    items.push({image:value})
  )
  const swiperOptions = {
    preloadImages: true,
    autoplay: 3000,
    autoplayDisableOnInteraction: false
  };
  return(
    <ReactSwiper swiperOptions={swiperOptions} showPagination items={items}
                className="carousel" />
  )
}



